.item-container {
    display: flex;
    justify-content:space-between;
    align-content: center;
    background-color: #CBFFFF;
    border-radius: 20px;
    border: 1px solid silver;
    margin-bottom: 50px;
    flex-direction: row;
}

.conteyner{
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    height: 80vh;
}
  h1 {
    font-size: 50px;
    font-family: Klee One bold;
    text-transform: uppercase;
    color: #3366FF;
    text-shadow: 2px 2px 4px black;

}

.conteyner iframe{
    width: 80vh;
    height: 70vh;
    margin: 5vh;
    border-radius: 20px;
}

.main{
    width: 90%;
}



.conteyner img {
    width: 80vh;
    height: 70vh;
    border: 1px solid rgba(228, 232,232, 0.5);
    border-radius: 20px;
    z-index: 1;
    margin: 5vh;
}

.conteyner h2 {
    display: flex;
    text-transform: uppercase;
    color: #860997;
    font-size: 30px;
    font-family: Klee One bold;
    z-index: 10;
    padding: 10px;
    align-items: center;
    margin: 5vh;

}

.conteyner p {
    margin-right: 5vh;
}

@media screen and (max-width: 940px){
    .main-container{

        margin: 10%;
    }

    .conteyner img {
        width: 80%;
        height: 80%;
        margin: 2vh;
    }
    .conteyner {

        width: 100vh;
        height: 25vh;
    }
    .main{
        width: 100%;
    }

    h1 {
        font-size: 30px;
    }

    .conteyner h2 {
        font-size: 10px;
        margin: 3px;
    }
    .conteyner p {
        font-size: 8px;
        margin: 2vh;
    }
    .conteyner iframe{
        width: 80%;
        height: 80%;
        margin: 2vh;
    }
}